import React, {useEffect, useState} from 'react';
import styles from './FacilityList.module.css';
import Box from "../../../components/Box/Box";

export default function FacilityList(props) {
    const [token, setToken] = useState('');
    useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);
        const tokenParam = urlParams.get('token');
        if (tokenParam) {
            setToken(tokenParam);
        }
    }, []);
    const facilityList = [
        {seq: 1 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~ 18:00', type: 'golf' },
        {seq: 2 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~ 18:00', type: 'screen_golf' },
        {seq: 3 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~ 18:00', type: 'lesson' },
        {seq: 4 ,name: '', bookingStatus: ' ', operatingHours: '  ', type: 'guesthouse' },
        {seq: 5 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~  11:00', type: 'cafeteria' },
        {seq: 6 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~ 18:00', type: 'sauna' },
        {seq: 7 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~ 18:00', type: 'pilates' },
        {seq: 8 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~ 18:00', type: 'room' },
        {seq: 9 ,name: '', bookingStatus: ' ', operatingHours: '24', type: 'study_room' },
        {seq: 10 ,name: '', bookingStatus: ' ', operatingHours: '09:00 ~  11:30', type: 'fitness' }

    ]
    return (
        <div className={styles.wrapper}>
            <div className={styles.title}> </div>
            <div className={styles.cardListWrapper}>
                {
                    facilityList.map((facility) => {
                        return (<Box {...facility}/>);
                    })
                }
            </div>
        </div>
    );
}
