// routes.js
import MyAptMain from "../pages/Main/MyAptMain/MyAptMain";
import Front from "../pages/Apt/Front/Front";
import FacilityReservationDetail from "../pages/Main/FacilityReservationDetail/FacilityReservationDetail";
import FacilityReservationPage from "../pages/Main/FacilityReservationPage/FacilityReservationPage";
import FacilityReservationPageForm from "../pages/Main/FacilityReservationPageForm/FacilityReservationPageForm";

const routes = [
    {
        path: '/',
        element: <MyAptMain/>,
        errorElement: <p>Not Found </p>
    },
    {
        path: '/facilityReservation/:seq',
        element: <FacilityReservationDetail/>,
        errorElement: <p>Not Found </p>
    },
    {
        path: '/facilityReservation/page/:name/:seq',
        element: <FacilityReservationPage />,
        errorElement: <p>Not Found </p>
    },
    {
        path: '/facilityReservation/page/form/:name/:seq',
        element: <FacilityReservationPageForm />,
        errorElement: <p>Not Found </p>
    },
    {
        path: '/apt/:seq',
        element: <Front />,
        errorElement: <p>Not Found </p>
    },
    {
        path: '/membership',
        element: <Front />,
        errorElement: <p>Not Found </p>
    }
];

export default routes;
