import React from 'react';
import styles from './Box.module.css'
import Icon from "../Icon/Icon";
import {useNavigate} from "react-router-dom";


export default function Box(props) {
    const {seq, name, bookingStatus, operatingHours, type} = props;
    const navigate = useNavigate();
    const handleClick = () => {
        console.log('clicked', props);
        navigate(`/facilityReservation/page/${name}/${seq}`, {state: {facilityInfo: props}});
    }
    return (
        <>
            <article className={styles.wrapper} onClick={handleClick}>
                <div className={styles.title}>
                    {name}
                </div>
                <div className={styles.description}>
                    <div>{bookingStatus}</div>
                    <div>{operatingHours}</div>
                </div>
                <div className={styles.icon}>
                    <Icon name={type}/>
                </div>
            </article>
        </>
    );
}
