import React from 'react';
//  
import {ReactComponent as IconGolf} from '../../assets/icons/facility/icon_golf.svg';
import {ReactComponent as IconCafeteria} from '../../assets/icons/facility/icon_cafeteria.svg';
import {ReactComponent as IconFitness} from '../../assets/icons/facility/icon_fitness.svg';
import {ReactComponent as IconGuesthouse} from '../../assets/icons/facility/icon_guesthouse.svg';
import {ReactComponent as IconInvitation} from '../../assets/icons/facility/icon_invitation.svg';
import {ReactComponent as IconPilates} from '../../assets/icons/facility/icon_pilates.svg';
import {ReactComponent as IconStudyRoom} from '../../assets/icons/facility/icon_study_room.svg';
import {ReactComponent as IconSauna} from '../../assets/icons/facility/icon_sauna.svg';
import {ReactComponent as IconScreenGolf} from '../../assets/icons/facility/icon_screen_golf.svg';
//  
import {ReactComponent as IconChevronRight } from '../../assets/icons/common/chevron_right.svg';

// Button Icon
import {ReactComponent as IconX} from '../../assets/icons/common/icon_x.svg';
import {ReactComponent as IconCheck} from '../../assets/icons/common/icon_check.svg';
import {ReactComponent as IconCalendar} from '../../assets/icons/common/icon_calendar.svg';
import {ReactComponent as IconCheckCircle} from '../../assets/icons/common/icon_check_circle.svg';
import {ReactComponent as IconCheckBox} from '../../assets/icons/common/icon_check_box.svg';
import {ReactComponent as IconClock} from '../../assets/icons/common/icon_clock.svg';

export default function Icon(props) {
    const {name, className} = props;
    let IconComponent;
    switch (name) {
        case 'cafeteria':
            IconComponent = IconCafeteria;
            break;
        case 'fitness':
            IconComponent = IconFitness;
            break;
        case 'guesthouse':
            IconComponent = IconGuesthouse;
            break;
        case 'golf':
            IconComponent = IconGolf;
            break;
        case 'lesson':
            IconComponent = IconInvitation;
            break;
        case 'pilates':
            IconComponent = IconPilates;
            break;
        case 'room':
            IconComponent = IconStudyRoom;
            break;
        case 'sauna':
            IconComponent = IconSauna;
            break;
        case 'screen_golf':
            IconComponent = IconScreenGolf;
            break;
        case 'chevron_right':
            IconComponent = IconChevronRight;
            break;
        case 'x':
            IconComponent = IconX;
            break;
        case 'check':
            IconComponent = IconCheck;
            break;
        case 'calendar':
            IconComponent = IconCalendar;
            break;
        case 'check_circle':
            IconComponent = IconCheckCircle;
            break;
        case 'clock':
            IconComponent = IconClock;
            break;
        case 'check_box':
            IconComponent = IconCheckBox;
            break;
        default:
            IconComponent = IconCafeteria;
            break;
    }


    return (
        <IconComponent/>
    );
}
