import React from 'react';
import styles from './NumberBox.module.css'
import Icon from "../Icon/Icon";
export default function NumberBox(props) {
    const { status, number, selected } = props;
    let text = '';
    let name = '';
    const getStatusClassName = () => {
        switch (status) {
            case 'available':
                text = ' ';
                name = selected ? 'check_box' :'';
                return styles.available;
            case 'unavailable':
                text = ' ';
                name = 'clock';
                return styles.unavailable;
            case 'maintenance':
                text = ' ';
                name = 'clock';
                return styles.maintenance;
            default:
                return '';
        }
    };
    const handleClick = () => {
        if (status === 'available') {
            console.log('available click');
            props.onClick(number);
        }
    }

    return (
        <button className={`${styles.gridItem} ${getStatusClassName()} ${selected ? styles.selected : ''}`} onClick={handleClick}>
            <div className={styles.squareContentText}>{text}</div>
            <div className={styles.squareContentNumber}>{number}</div>
            {name && <div className={`${styles.squareContentIcon}  ${selected ? styles.selectedIcon : ''}`}><Icon name={name}/></div>}
        </button>
    );
}
