import React from 'react';
import styles from './Button.module.css';
import Icon from "../Icon/Icon";


export default function Button(props) {
    const { name, type, bgColor, color, icon, onClick } = props;

    //   .
    const buttonStyle = {
        backgroundColor: bgColor || 'default_bg_color', // bgColor     .
        color: color || 'default_color', // color     .
    };

    function handleClick() {
        if (onClick) {
            console.log(onClick);
            onClick();
        }
    }

    return (
        <button className={styles.buttonWrapper} type={type} style={buttonStyle} onClick={handleClick}>
            {icon && <Icon name={icon}/>}
            <div>{name}</div>
        </button>
    );
}
