import React from 'react';
import styles from './TimeBox.module.css'
import Icon from "../Icon/Icon";
export default function TimeBox(props) {
    const { status, start, end, selected } = props;
    let text = '';
    let name = '';
    const getStatusClassName = () => {
        switch (status) {
            case 'available':
                return styles.available;
            case 'unavailable':
                return styles.unavailable;
            case 'maintenance':
                return styles.maintenance;
            default:
                return '';
        }
    };
    const handleClick = () => {
        if (status === 'available') {
            props.onClick(start);
        }
    }

    return (
        <button className={`${styles.gridItem} ${getStatusClassName()} ${selected ? styles.selected : ''}`} onClick={handleClick}>
            <div className={styles.squareContentNumber}>{start}</div>
            {name && <div className={`${styles.squareContentIcon}  ${selected ? styles.selectedIcon : ''}`}><Icon name={name}/></div>}
        </button>
    );
}
