import React from 'react';
import styles from './ListItem.module.css'
import Icon from "../Icon/Icon";
import {useNavigate} from "react-router-dom";

export default function ListItem(props) {
    const {seq, name, bookingStatus, resDate, fcDetail} = props;
    const navigate = useNavigate();
    const handleClick = () => {
        console.log('clicked', props);
        navigate(`/facilityReservation/${seq}`, {state: {facilityInfo: props}});
    }

    return (
        <article className={styles.wrapper} onClick={handleClick} >
            <div className={styles.fcHeader}>
                <div className={styles.name}>{name}</div>
                <div className={styles.status}>{bookingStatus}</div>
            </div>
            <div className={styles.fcResDate}>{resDate}</div>
            <div className={styles.fcDetail}>{fcDetail}</div>
            <div className={styles.button}><Icon name={'chevron_right'}/></div>
        </article>
    );
}
