import React, {useEffect, useState} from 'react';
import styles from './FacilityReservationPageForm.module.css'
import Calendar from "../../../components/Calendar/Calendar";
import Icon from "../../../components/Icon/Icon";
import KeyValueListItem from "../../../components/KeyValueListItem/KeyValueListItem";
import Button from "../../../components/Button/Button";
import NumberBox from "../../../components/NumberBox/NumberBox";
import TimeBox from "../../../components/TimeBox/TimeBox";

export default function FacilityReservationPageForm(props) {
    const today = new Date();
    const [showCalendar, setShowCalendar] = useState(false);
    const [selectDate, setSelectDate] = useState('');
    const [selectedNumber, setSelectedNumber] = useState(null);
    const [selectTime, setSelectTime] = useState('');
    const [timeArray, setTimeArray] = useState([]);
    const [eachTimeArray, setEachTimeArray] = useState([]);
    const [selectedIndexes, setSelectedIndexes] = useState([]);
    const [combinedTimeRange, setCombinedTimeRange] = useState("");
    const [usage, setUsage] = useState("");

    const limit = 120;
    const duration = 10;
    const reservedTimes = ['10:00~10:30', '11:00~12:00'];
    const numberBoxArray = [{status: 'available', number: 1},
        {status: 'unavailable', number: 2},
        {status: 'maintenance', number: 3},
        {status: 'available', number: 4},
        {status: 'available', number: 5},
        {status: 'available', number: 6},
        {status: 'unavailable', number: 7},
        {status: 'available', number: 8},
        {status: 'maintenance', number: 9},
    ];


    function getReservationTime(start, end, interval, reservedTimes) {
        const timeArray = [];
        const startTime = new Date(`1970-01-01T${start}:00`);
        const endTime = new Date(`1970-01-01T${end}:00`);

        const reservedTimeRanges = reservedTimes.map(time => time.split('~').map(t => new Date(`1970-01-01T${t}:00`)));

        while (startTime < endTime) {
            const startTimeStr = formatTime(startTime);
            startTime.setMinutes(startTime.getMinutes() + interval);
            const endTimeStr = formatTime(startTime);

            const timeSlot = `${startTimeStr}~${endTimeStr}`;
            const isReserved = reservedTimeRanges.some(([start, end]) => startTime > start && startTime <= end);

            const status = isReserved ? 'disabled' : 'available';

            timeArray.push({status, time: timeSlot});
        }

        function formatTime(time) {
            const hours = time.getHours().toString().padStart(2, '0');
            const minutes = time.getMinutes().toString().padStart(2, '0');
            return `${hours}:${minutes}`;
        }

        return timeArray;
    }

    function getReservationEachTime(start, end, interval, reservedTimes) {
        const timeArray = [];
        const startTime = new Date(`1970-01-01T${start}:00`);
        const endTime = new Date(`1970-01-01T${end}:00`);

        const reservedTimeRanges = reservedTimes.map(time => time.split('~').map(t => new Date(`1970-01-01T${t}:00`)));

        while (startTime < endTime) {
            const startTimeStr = formatTime(startTime);
            startTime.setMinutes(startTime.getMinutes() + interval);
            const endTimeStr = formatTime(startTime);

            const isReserved = reservedTimeRanges.some(([start, end]) => startTime > start && startTime <= end);

            const status = isReserved ? 'disabled' : 'available';

            timeArray.push({status, start: startTimeStr, end: endTimeStr});
        }

        function formatTime(time) {
            const hours = time.getHours().toString().padStart(2, '0');
            const minutes = time.getMinutes().toString().padStart(2, '0');
            return `${hours}:${minutes}`;
        }

        return timeArray;
    }


    const getCombinedTimeRange = (selectedIndexes) => {
        if (selectedIndexes.length === 0) {
            return "";
        }

        const firstIndex = selectedIndexes[0];
        const lastIndex = selectedIndexes[selectedIndexes.length - 1];

        const [firstStart, firstEnd] = timeArray[firstIndex].time.split("~");
        const [lastStart, lastEnd] = timeArray[lastIndex].time.split("~");

        return `${firstStart}~${lastEnd}`;
    };

    const handleTimeClick = (index) => {
        const maxMinutes = limit; //  120 
        const maxSelections = maxMinutes / duration; //    

        const isConsecutive = (currentIndex, selectedIndexArray) => {
            for (let i = 0; i < selectedIndexArray.length; i++) {
                const prevIndex = selectedIndexArray[i];
                const prevTime = timeArray[prevIndex].time;
                const [prevStart, prevEnd] = prevTime.split("~");
                const [nextStart, nextEnd] = timeArray[currentIndex].time.split("~");
                const diffInMinutes = (new Date(`1970-01-01T${nextStart}:00`) - new Date(`1970-01-01T${prevEnd}:00`)) / (1000 * 60);

                if (diffInMinutes === 0) {
                    return true;
                }
            }
            return false;
        };

        if (selectedIndexes.length === 0) {
            setSelectedIndexes([index]);
        } else {
            if (selectedIndexes.includes(index)) {
                if (index === selectedIndexes[0] || index === selectedIndexes[selectedIndexes.length - 1]) {
                    setSelectedIndexes(selectedIndexes.filter((selectedIndex) => selectedIndex !== index));
                }
            } else {
                if (selectedIndexes.length < maxSelections && isConsecutive(index, selectedIndexes)) {
                    setSelectedIndexes([...selectedIndexes, index].sort((a, b) => a - b));
                } else {
                    setSelectedIndexes([index]);
                }
            }
        }
    };
    useEffect(() => {
        setCombinedTimeRange(getCombinedTimeRange(selectedIndexes));
    }, [selectedIndexes]);

    useEffect(() => {
        setTimeArray(getReservationTime('09:00', '24:00', duration, reservedTimes));
    }, []);
    useEffect(() => {
        setEachTimeArray(getReservationEachTime('09:00', '24:00', 60, reservedTimes));
    }, []);
    const handleCalendar = () => {
        setShowCalendar(!showCalendar);
    }
    const handleClickCalendar = (date) => {
        setSelectDate(date);
    }
    const handleNumberBoxClick = (number) => {
        if (selectedNumber === number) {
            setSelectedNumber(null);
        } else {
            setSelectedNumber(number);
        }
    };
    const handleUsageButtonClick = (e) => {
        console.log('1 (1)  ');
        setUsage(e.target.innerText);

    }
    const handleReservationButtonClick = () => {
        console.log('  ');
        console.log('  : ', selectDate);
        console.log('  : ', combinedTimeRange);
        console.log('  : ', selectedNumber);
        console.log('   : ', usage);
    }

    return (
        <section className={styles.wrapper}>
            <article className={styles.dateWrapper}>
                <div className={styles.wrap}>
                    <div className={styles.dateTitle}></div>
                    <div className={styles.date}>{selectDate}</div>
                </div>
                <button className={styles.button} onClick={handleCalendar}>
                    <Icon name={'calendar'}/>
                </button>

            </article>
            {showCalendar && <Calendar onClick={handleClickCalendar}/>}
            <KeyValueListItem keyName={' '} value={usage}/>
            <div className={styles.usageWrapper}>
                <button className={styles.usageButton} onClick={handleUsageButtonClick}>1 (1)</button>
                <button className={styles.usageButton} onClick={handleUsageButtonClick}>1 (12)</button>
            </div>
            <KeyValueListItem keyName={' '} value={usage}/>
            <div className={styles.timeUsedWrapper}>
                {eachTimeArray.map((item, index) => {
                    return (
                        <TimeBox {...item} />
                    )
                })}

                {/*<button key={index} disabled={item.status === 'disabled' ? 'disabled' : ''}*/}
                {/*        className={`${styles.timeButton} ${selectedIndexes.includes(index) ? styles.selected : ""}`}*/}
                {/*        onClick={() => handleTimeClick(index)}>*/}
                {/*    {item.time}*/}
                {/*    {selectedIndexes.includes(index) && <Icon name={'check'}/>}*/}
                {/*</button>*/}

            </div>

            <KeyValueListItem keyName={' '} value={combinedTimeRange}/>
            <div className={styles.timeZone}>
                <div className={styles.desc}> {limit / 60}  </div>
                {timeArray.map((item, index) => {
                    return (
                        <button key={index} disabled={item.status === 'disabled' ? 'disabled' : ''}
                                className={`${styles.timeButton} ${selectedIndexes.includes(index) ? styles.selected : ""}`}
                                onClick={() => handleTimeClick(index)}>
                            {item.time}
                            {selectedIndexes.includes(index) && <Icon name={'check'}/>}
                        </button>
                    )
                })}
            </div>
            <KeyValueListItem keyName={' '} value={`${selectedNumber ? selectedNumber + ' ' : ''} `}/>
            <div className={styles.numberBoxWrapper}>
                {
                    numberBoxArray.map((item, index) => {
                        return (
                            <NumberBox
                                key={index}
                                status={item.status}
                                number={item.number}
                                selected={selectedNumber === item.number}
                                onClick={handleNumberBoxClick}
                            />
                        )
                    })
                }
            </div>
            <div className={styles.buttonWrapper}>
                <Button icon={'check_circle'} name=" " type="submit" bgColor="var(--color-primary)"
                        color="white" onClick={handleReservationButtonClick} />
            </div>
        </section>
    );
}
